* {
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  background: #FFF;
  font-family: "Helvetica", Arial, sans-serif;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  width: 100%;
  max-width: 850px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 40px;
}

:root {
  --color-dark: #0c2742;
  --color-blue: #1b75bc;
  --color-light-blue: #eef2f5;
  --color-white: #fff;
  --color-yellow: #f9ee8e;
  --color-green: #02ad23;
}

* {
  padding: 0;
  margin: 0;
}

.header {
  height: 90px;
  font-family: "Oswald", sans-serif;
  transition: all 0.2s linear;
}
.header .container {
  max-width: 1400px;
}

.fixed .header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  background-color: var(--color-white);
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.svg-image-header-logo,
.svg-image-header-logo-black {
  width: 174px;
  height: auto;
}

.svg-image-header-logo-black {
  display: none;
}

.fixed .header .svg-image-header-logo-black {
  display: block;
}
.fixed .header .svg-image-header-logo {
  display: none;
}
.fixed .header .header__link {
  color: var(--color-black);
}
.fixed .header .header__link.active.active {
  color: var(--color-white);
}
.fixed .header .header__appply {
  color: var(--color-white);
  background-color: var(--color-blue);
}

.header__navbar {
  height: 90px;
}
.header__navbar .header__list {
  height: 100%;
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.header__link {
  height: 100%;
  padding: 28px 11px;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  color: var(--color-white);
}
.header__link:last-child {
  margin: 0;
}
.header__link.active {
  color: var(--color-white);
  background-color: var(--color-blue);
}
.header__link a {
  display: block;
  padding: 5px 10px;
  cursor: pointer;
}

.header__appply {
  padding: 0;
  height: 56px;
  color: var(--color-blue);
  background-color: var(--color-white);
  border-radius: 3px;
}
.header__appply a {
  display: block;
  padding: 16px 24px;
}

.first {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  min-height: 900px;
}
.first.fixed {
  padding-top: 90px;
}

.first__bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  overflow: hidden;
}
.first__bg video {
  position: absolute;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 100%;
}

.intro {
  flex-grow: 1;
}
.intro .container {
  max-width: 1328px;
}

.intro__wrapper {
  min-height: calc(100vh - 90px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 170px 0 100px 27px;
}

.intro__title {
  font-family: "Oswald", sans-serif;
  margin: 0;
  font-weight: 400;
  font-size: 120px;
  line-height: 100px;
  letter-spacing: 0.4px;
  color: var(--color-white);
  text-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}
.intro__title p {
  margin: 0;
}
.intro__title .intro__static {
  margin-bottom: 24px;
}
.intro__title .intro__frame {
  height: 120px;
  overflow: hidden;
}
.intro__title .intro__dynamic {
  color: var(--color-yellow);
  transform: translateY(120px);
  opacity: 0;
  animation: TextSlider 10s linear 0.5s infinite;
  animation-fill-mode: forwards;
}
.intro__title .intro__dynamic p {
  margin-bottom: 20px;
}
.intro__title .intro__dynamic p:last-child {
  margin-bottom: 0;
}

.intro__info {
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.intro__about {
  max-width: 596px;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 38px;
  letter-spacing: 0.4px;
  color: rgba(255, 255, 255, 0.8);
}

.intro__scroll {
  display: flex;
  align-items: center;
  transform: rotate(90deg);
  animation: Rotate 0.5s linear 0.5s;
  animation-fill-mode: forwards;
  opacity: 0;
}
.intro__scroll p {
  margin-left: 12px;
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 38px;
  letter-spacing: 0.4px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.8);
}

@keyframes Rotate {
  0% {
    transform: rotate 90deg;
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: rotate(0);
    opacity: 1;
  }
}
@keyframes TextSlider {
  0% {
    opacity: 0;
    transform: translateY(120px);
  }
  2% {
    opacity: 1;
  }
  8% {
    transform: translateY(0);
  }
  16% {
    transform: translateY(0);
  }
  24% {
    transform: translateY(-120px);
  }
  32% {
    transform: translateY(-120px);
  }
  40% {
    transform: translateY(-240px);
  }
  98% {
    opacity: 1;
  }
  100% {
    transform: translateY(-240px);
    opacity: 0;
  }
}
.main {
  flex-grow: 1;
  overflow: hidden;
}

body {
  overflow-x: hidden;
  background-color: var(--color-light-blue);
}

.about {
  position: relative;
}

.about__container {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: flex-end;
}

.about__wrapper {
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  display: grid;
  align-items: flex-end;
  grid-template-areas: "title info" "description info";
  grid-template-columns: 41.66% 48.89%;
  gap: 64px 9.45%;
  padding: 108px 0 238px 12.68%;
}

.about__title {
  justify-self: end;
  max-width: 482px;
  grid-area: title;
  margin-left: 20.75%;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 90px;
  line-height: 100px;
  letter-spacing: 0.4px;
  color: var(--color-dark);
}
.about__title span {
  color: var(--color-blue);
}

.about__description {
  max-width: 408px;
  grid-area: description;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 38px;
  letter-spacing: 0.2px;
  color: var(--color-dark);
}

.about__info {
  grid-area: info;
  padding-top: 164px;
  justify-self: end;
}

.about__img img {
  display: block;
  width: 100%;
  height: auto;
}

.about__more {
  position: relative;
}

.about__square {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 174px;
  height: 174px;
  background-color: var(--color-blue);
  border: 1px solid #fff;
  transform: rotate(45deg) translate(-123px, 0px);
  filter: drop-shadow(4px 4px 40px rgba(27, 117, 188, 0.4));
}

.about__text {
  position: absolute;
  top: -30px;
  left: -38px;
  z-index: 2;
  width: 80px;
  height: 68px;
  text-align: center;
}
.about__text p {
  margin-bottom: 4px;
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  text-align: justify;
  letter-spacing: 0.8px;
  text-transform: uppercase;
  color: var(--color-white);
}

.about__link {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 10;
  cursor: pointer;
}

.about__wrapper:nth-child(2) {
  display: grid;
  grid-template-areas: "info title " "info description";
  grid-template-columns: 46.75% 48.81%;
  gap: 73px 4.44%;
  margin: 0;
  padding: 157px 0 238px 0;
}
.about__wrapper:nth-child(2) .about__title {
  max-width: 506px;
  margin-left: 0;
}
.about__wrapper:nth-child(2) .about__description {
  justify-self: end;
  max-width: 480px;
  margin-left: 102px;
}
.about__wrapper:nth-child(2) .about__info {
  padding-top: 0;
  align-content: flex-end;
}
.about__wrapper:nth-child(2) .about__img {
  max-width: 540px;
}
.about__wrapper:nth-child(2) .about__square {
  left: 100%;
}
.about__wrapper:nth-child(2) .about__text {
  left: auto;
  right: -38px;
}

.svg-image-pathnew {
  position: absolute;
  top: 16px;
  left: 305px;
  height: auto;
  z-index: 1;
}

.build {
  position: relative;
  z-index: 1;
  height: 100vh;
  min-height: 900px;
  padding-top: 90px;
}

.build__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0 12.09%;
  padding: 0 19.06% 0 12.69%;
}

.build__info:first-child {
  padding-top: 220px;
}
.build__info:last-child {
  padding-top: 100px;
}

.build__hot {
  width: 128px;
  height: 28px;
  background-color: #fff;
  border-radius: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 16px;
  margin-bottom: 24px;
}
.build__hot img {
  width: 12px;
  height: 12px;
}
.build__hot p {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 10px;
  line-height: 24px;
  letter-spacing: 0.2px;
  text-transform: uppercase;
  color: var(--color-dark);
}

.build__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.build__item {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 20px 0 0;
  border-bottom: 1px dashed rgba(255, 255, 255, 0.4);
  transition: all 0.2s linear;
}
.build__item:hover {
  background-color: var(--color-white);
}
.build__item:hover .build__position {
  color: var(--color-dark);
  transform: translateX(20px);
}
.build__item:hover .build__place {
  color: var(--color-dark);
  transform: translateX(20px);
}
.build__item:hover .svg-image-arrow-blue {
  opacity: 1;
}

.build__description {
  width: 73%;
}

.build__position {
  margin-bottom: 6px;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 32px;
  letter-spacing: 0.2px;
  color: var(--color-white);
  transition: all 0.2s linear;
}

.build__place {
  padding-bottom: 20px;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.2px;
  color: rgba(255, 255, 255, 0.8);
  transition: all 0.2s linear;
}

.build__link {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  cursor: pointer;
}

.svg-image-arrow-blue {
  opacity: 0;
  transition: all 0.2s linear;
}

.build__title {
  margin-bottom: 40px;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 90px;
  line-height: 100px;
  letter-spacing: 0.4px;
  color: var(--color-white);
}

.build__text {
  margin-bottom: 41px;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 32px;
  letter-spacing: 0.2px;
  color: var(--color-white);
}

.build__more {
  position: relative;
  max-width: 170px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.2s linear;
}
.build__more:hover {
  transform: translateX(5px);
}
.build__more p {
